import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {Images} from "games/speedread/speedwords/assets"

const durations = {
  eye: 400
}

export class IntroComponent extends GameComponent {
  positions = Sizes.create({
    from: {x: 650, y: 580},
    to: {x: 550, y: 580},
  })
  bg = this.addImage(Images.bg_intro)
  eye = this.addImage(Images.bg_intro_eye).setPos(this.positions.from)

  playTween() {
    return Tweens.start({
      target: this.eye,
      to: this.positions.to,
      simpleYoyo: true,
      duration: durations.eye,
    })
  }

}




