import {CoreColors} from "core/assets/fonts"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {AnswerBtnBehavior} from "core/ui/controls/behaviors/answerBtnBehavior"
import {ImageBtn} from "core/ui/controls/btn"
import {Fonts, Images} from "games/speedread/speedwords/assets"
import {Task} from "../logic"

const biValues = BiValue.create({
  scale: 1
}, {
  scale: 0.8
})

export class AnswerComponent extends GameComponent implements OnInit {

  positions = Sizes.create({
    answerMarin: -50
  })
  biPositions = Sizes.bi({
    margin: {x: 720, y: 280, absolute: true},
  }, {
    margin: {x: 720, y: 230, absolute: true},
  })
  private buttons: ImageBtn[] = []

  onNewTask(task: Task): void {
    this.createButtons(task)
  }

  onAnswer(answer: ILastAnswer): void {
    const correctAnswer = (answer.levelTask.task as Task).answers[0]
    AnswerBtnBehavior.findByAnswerId(this.buttons, correctAnswer).textControl.fill = CoreColors.darkGreen
    if (AnswerStatus.Error == answer.status) {
      AnswerBtnBehavior.findByAnswerId(this.buttons, answer.answerId).textControl.fill = CoreColors.wrong
    }
  }

  createButtons(task: Task) {
    this.removeAll(true)
    this.buttons = task.questionWords.map(word => {
      const btn = new ImageBtn(word, this.gameKey(Images.plash_m), () => {
      }, Fonts.word)
      btn.tag == word
      return this.add(this.addAnswerBehavior(btn, word, {
        dark: true,
        margin: this.positions.answerMarin
      }).anchorCenter())
    })
    this.updatePositions()
    this.answersAreReady()
  }

  updatePositions() {
    const colCount = this.isPortrait ? 1 : 2
    Sizes.tableFromList(this.buttons, colCount, this.current(this.biPositions.margin))
    this.updatePivot()
  }

  onInit(): void {
    this.onBiValueChange(biValues.scale, (c, v) => {
      c.symmetricScale = v
      this.updatePositions()
    })
  }

}



