import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Control} from "core/ui/controls/control"
import {Img} from "core/ui/controls/img"
import {Txt} from "core/ui/controls/txt"
import {Fonts, Images, Sounds} from "games/speedread/speedwords/assets"
import * as _ from 'lodash'
import {Task} from "../logic"

const count = 9

type Props = {
  onComplete: () => {}
}

const biValues = BiValue.create({
  bg: {
    angle: 0,
    scale: 1
  }
}, {
  bg: {
    angle: 90,
    scale: 0.8
  }
})

export class QuestionComponent extends GameComponent<Props> implements OnInit {

  positions = Sizes.create({
    words: {y: -30}
  })
  biPositions = Sizes.bi({
    margin: {x: 450, y: 200, absolute: true},
  }, {
    margin: {x: 350, y: 100, absolute: true},
  })

  bg: Img
  group: Control
  textGroup: Control
  texts: Txt[]

  text = this.add(new Txt(""))

  onNewTask(task: Task): void {
    this.onIntroCompleteOrMemorize.addOnce(() => {
      this.disableInput(true)
      this.playSound(Sounds.type, true)
      this.showText(task, 0)
    })
  }

  showText(task: Task, index: number) {
    const delay = 60 * 1000 / task.speed
    if (index >= task.words.length) {
      this.delay(delay * 0.1).addOnce(() => {
        this.texts.forEach(x => x.visible = false)
        this.disableInput(false)
        this.props.onComplete()
        this.stopSound(Sounds.type)
      })
      return
    } else {
      this.texts.forEach((x, i) => {
        const isCurrent = i == index % count
        x.visible = isCurrent
        if (isCurrent) {
          x.text = task.words[index]
          x.updatePivot()
        }
      })
      this.delay(delay).addOnce(() => this.showText(task, index + 1))
    }
  }

  createControls() {
    this.bg = this.addImage(Images.plash_big).anchorCenter().fluentUpdate(c => {
      const v = this.current(biValues.bg)
      c.angle = v.angle
      c.symmetricScale = v.scale
    })
    this.group = this.add(new Control()).anchorCenter()
    this.textGroup = this.add(new Control()).anchorCenter()
      .updatePos(0, this.positions.words.y)
    const colCount = this.isPortrait ? 1 : 3
    const lines = _.range(count).map(() => this.group.add(this.makeImage(Images.chert)).anchorCenterX())
    const margin = this.current(this.biPositions.margin)
    Sizes.tableFromList(lines, colCount, margin)
    this.group.updatePivot()
    this.texts = _.range(count).map(() => this.textGroup.add(new Txt("", Fonts.word).anchorCenter()))
    Sizes.tableFromList(this.texts, colCount, margin)
    this.textGroup.updatePivot()
    this.updatePivot()

  }

  onInit(): void {
    this.onBiValueChange({} as any, () => {
      this.removeAll(true)
      this.createControls()
    })
  }

}



