import {GameLogic} from "core/logic/index"
import {Sys} from "core/logic/sys"
import {Arrays, RandomGenerator} from "core/utils/index"
import {SpeedwordsData} from "games/speedread/speedwords/data"
import {Level} from "./levels"

const minWordCount = 7
const maxWordCount = 18
const answerCount = 6
const tutorialSpeed = 100
const tutorialMinCount = 10
const tutorialMaxCount = 12

export type Task = {
  words: string[]
  speed: number,
  questionWords: string[]
  answers: string[]
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  levelWords = SpeedwordsData.words

  nextTask(level: Level) {
    const count = this.isTutorial
      ? this.rng.count(tutorialMinCount, tutorialMaxCount)
      : this.rng.count(maxWordCount, minWordCount)
    const words = this.rng.subset(this.levelWords, count)
    const [restWords, answers] = Arrays.split(words, words.length - 1)
    const questionWords = this.rng.shuffle(answers.concat(this.rng.subset(restWords, answerCount - 1)))
    const speed = this.isTutorial ? tutorialSpeed : level.speed

    const task: Task = {
      words,
      speed,
      questionWords,
      answers
    }
    if (Sys.showLog) {
      console.log("task", task)
    }
    return task
  }

}





